import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-invoice-discount',
  templateUrl: './invoice-discount.component.html',
  styleUrls: ['./invoice-discount.component.scss']
})
export class InvoiceDiscountComponent implements OnInit {

  constructor(
    private router:Router
  ) { }

  ngOnInit(): void {
  }

  gotoListDiscount(){
    this.router.navigate(['/sales/dashboard/manage/sales-program/discount-customer/invoice-discount/list-discount'])
  }

  gotoReportDiscount(){
    this.router.navigate(['/sales/dashboard/manage/sales-program/discount-customer/invoice-discount/show-report'])

  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/sales-program/discount-customer'])
  }


}
