import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { InvoiceDiscountComponent } from './invoice-discount.component'; 
import { RouterModule, Routes } from '@angular/router'; 
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { ListDiscountComponent } from './list-discount/list-discount.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
const routes: Routes = [
  {
    path: '',
    component: InvoiceDiscountComponent
  }, 
  {
    path: 'list-discount',
    component: ListDiscountComponent
  },
]


@NgModule({
  declarations: [
    InvoiceDiscountComponent,
    ListDiscountComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule
  ]
})
export class InvoiceDiscountModule { }
